export default {
	data() {
	return {
			PageCur: 'Home',
			cuIconList: [{
			    cuIcon: 'dfca',
				badge: 0,
				name: '龙井之窗',
				url: '/pages/index/index_longjing/index_longjing',
			}, {
				 cuIcon: 'dfcaade',
				badge: 0,
				name: '民呼我应',
				url: '/pages/index/index_people/index_people',
				
			},{
				cuIcon: 'dfdb', 
				badge: 0,
				name: '乐享生活',
				url: '/pages/index/index_play/index_play',
				
			},  {
			    cuIcon: 'dfcccb', 
				badge: 0,
				name: '益路有你',
				url: '/pages/index/index_volunteer/index_volunteer',
				
			}, {
				cuIcon: 'dfccdc', 
				badge: 0,
				name: '健康驿站',
				url: '/pages/index/index_healthy/index_healthy',
				
			}, {
				cuIcon: 'dfcade',
				badge: 0,
				name: '社区商圈',
				url: '/pages/index/index_shopping/index_shopping',
				
			}],
			modalName: null,
			gridCol: 3,
			gridBorder: false,
			menuBorder: false,
			menuArrow: false,
			menuCard: false,
			skin: false,
			listTouchStart: 0,
			listTouchDirection: null,
		}
	},
	methods: {
		NavChange: function(e) {
			this.PageCur = e.currentTarget.dataset.cur
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		Gridchange(e) {
			this.gridCol = e.detail.value
		},
		Gridswitch(e) {
			this.gridBorder = e.detail.value
		},
		MenuBorder(e) {
			this.menuBorder = e.detail.value
		},
		MenuArrow(e) {
			this.menuArrow = e.detail.value
		},
		MenuCard(e) {
			this.menuCard = e.detail.value
		},
		SwitchSex(e) {
			this.skin = e.detail.value
		},
			
		// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX
		},
			
		// ListTouch计算方向
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
		},
			goMore(url) {
				uni.navigateTo({
					url: url
				});
			},
		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target
			} else {
				this.modalName = null
			}
			this.listTouchDirection = null
		}
	}	
}
